.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .medical__devices {
  padding-top: 25px;
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .medical__devices .container {
  grid-area: container;
  display: grid;
  grid-template-areas: "aside breadcrumb breadcrumb" "aside catalog popProd" "order order order";
  grid-template-columns: 25% 60% 15%;
}
body .medical__devices .container .content__catalog__breadcrumb {
  grid-area: breadcrumb;
}
body .medical__devices .container .content__catalog__breadcrumb ul {
  display: flex;
}
body .medical__devices .container .content__catalog__breadcrumb ul li a {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .medical__devices .container .content__catalog__breadcrumb ul li span {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .medical__devices .container .content__catalog {
  grid-area: catalog;
}
body .medical__devices .container .content__catalog__medicalDevices h5 {
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
}
body .medical__devices .container .content__catalog__medicalDevices div h6 {
  margin: 25px 0;
  font: 1.125em/1.25em "PTSans-Regular";
  color: #252525;
}
body .medical__devices .container .content__catalog__medicalDevices div h6 i {
  color: #176975;
}
body .medical__devices .container .content__catalog__medicalDevices div h6:hover {
  cursor: pointer;
}
body .medical__devices .container .content__catalog__medicalDevices div ul li {
  margin-left: 50px;
}
body .medical__devices .container .content__catalog__medicalDevices__info {
  padding-top: 115px;
}
body .medical__devices .container .content__catalog__medicalDevices__info h5 {
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
}
body .medical__devices .container .content__catalog__medicalDevices__info p {
  margin: 20px 0;
  padding: 0 150px 0 0;
  font: 1.125em/1.25em "PTSans-Regular";
  color: #252525;
}
body .medical__devices .container .content__popularProducts {
  grid-area: popProd;
  padding-top: 150px;
}
body .medical__devices .container .content__popularProducts a {
  display: block;
  font: 1em/1.125em "PTSans-Bold";
  color: #0e3a40;
  text-align: center;
  text-decoration: none;
}
body .medical__devices .container .content__popularProducts ul li {
  margin: 25px 0;
  width: 250px;
  height: 350px;
  position: relative;
  text-align: center;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
body .medical__devices .container .content__popularProducts ul li a {
  display: block;
  text-transform: none;
}
body .medical__devices .container .content__popularProducts ul li a img {
  margin-top: 15px;
  width: 200px;
  height: 200px;
}
body .medical__devices .container .content__popularProducts ul li a p {
  padding: 10px;
  font: 1em/1.125em "PTSans-Regular";
  color: #0e3a40;
  text-align: left;
}
body .medical__devices .container .content__popularProducts ul li a .price {
  font: 1.25em/1.375em "PTSans-Bold";
}
body .medical__devices .container .content__popularProducts ul li a .price span {
  font: 0.875em/1em "PTSans-Regular";
}
body .medical__devices .container .content__popularProducts ul li .hover {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 50px;
  display: none;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  left: -1px;
  bottom: -45px;
  z-index: 2;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-top: none;
}
body .medical__devices .container .content__popularProducts ul li .hover .addBusket,
body .medical__devices .container .content__popularProducts ul li .hover .addFavorite {
  margin: 25px 5px 10px;
  display: block;
  height: 40px;
  position: relative;
  font: 18px/40px "PTSans-Regular";
  border: none;
  border-radius: 5px;
  outline: none;
  z-index: 1;
}
body .medical__devices .container .content__popularProducts ul li .hover .addBusket:hover,
body .medical__devices .container .content__popularProducts ul li .hover .addFavorite:hover {
  cursor: pointer;
}
body .medical__devices .container .content__popularProducts ul li .hover .addBusket {
  width: 150px;
  color: #fff;
  background-color: #00929e;
}
body .medical__devices .container .content__popularProducts ul li .hover .addBusket span {
  margin: 0 0 0 10px;
}
body .medical__devices .container .content__popularProducts ul li .hover .addFavorite {
  width: 40px;
  color: #0e3a40;
  background-color: #fff;
  border: 1px solid #0e3a40;
  box-sizing: border-box;
}
body .medical__devices .container .content__popularProducts ul li:hover {
  cursor: pointer;
}
body .medical__devices .container .content__popularProducts ul li:hover .hover {
  display: flex;
  cursor: default;
}
body .medical__devices .container .content__howToMakeOrder {
  grid-area: order;
}
