@import "../components/variables";
@import "../components/mixin";

body {
  .medical__devices {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb breadcrumb"
                           "aside catalog popProd"
                           "order order order";
      grid-template-columns: 25% 60% 15%;
      .content__catalog__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__catalog {
        grid-area: catalog;
        &__medicalDevices {
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          div {
            h6 {
              margin: 25px 0;
              font: 1.125em/1.25em @sansregular;
              color: #252525;
              i {
                color: @fontColor;
              }
              &:hover {
                cursor: pointer;
              }
            }
            ul {
              li {
                margin-left: 50px;
              }
            }
          }
        }
        &__medicalDevices__info {
          padding-top: 115px;
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          p {
            margin: 20px 0;
            padding: 0 150px 0 0;
            font: 1.125em/1.25em @sansregular;
            color: #252525;
          }
        }
      }
      .content__popularProducts {
        grid-area: popProd;
        padding-top: 150px;
        a {
          display: block;
          font: 1em/1.125em @sansbold;
          color: @linkColor;
          text-align: center;
          text-decoration: none;
        }
        ul {
          li {
            margin: 25px 0;
            width: 250px;
            height: 350px;
            position: relative;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            a{
              display: block;
              text-transform: none;
              img {
                margin-top: 15px;
                width: 200px;
                height: 200px;
              }
              p {
                padding: 10px;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-align: left;
              }
              .price {
                font: 1.25em/1.375em @sansbold;
                span {
                  font: .875em/1em @sansregular
                }
              }
            }
            .hover{
              padding: 0;
              margin: 0;
              width: 100%;
              height: 50px;
              display: none;
              flex-direction: row-reverse;
              justify-content: center;
              align-items: flex-end;
              position: absolute;
              left: -1px;
              bottom: -45px;
              z-index: 2;
              background-color: #fff;
              border: 1px solid #e6e6e6;
              border-top: none;
              .addBusket,
              .addFavorite{
                margin: 25px 5px 10px;
                display: block;
                height: 40px;
                position: relative;
                font: 18px/40px @sansregular;
                border: none;
                border-radius: 5px;
                outline: none;
                z-index: 1;
                &:hover{
                  cursor: pointer;
                }
              }
              .addBusket{
                width: 150px;
                color: #fff;
                background-color: @mainColor;
                span{
                  margin: 0 0 0 10px;
                }
              }
              .addFavorite{
                width: 40px;
                color: @linkColor;
                background-color: #fff;
                border: 1px solid @linkColor;
                box-sizing: border-box;
              }
            }
            &:hover {
              cursor: pointer;
              .hover{
                display: flex;
                cursor: default;
              }
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
      }
    }
  }
}